import { createRouter, createWebHistory } from "vue-router";
import HomePage from "../components/HomePage.vue";
import MePage from "../components/MePage.vue";
import WorkPage from "../components/WorkPage.vue";
import IdentityPage from "../components/IdentityPage.vue";
import NudayPage from "@/components/NudayPage.vue";
import FunPage from "@/components/FunPage.vue";
import StableInstallPage from "@/components/StableInstallPage.vue";
import UseDP from "@/components/DesignPrompt.vue";
import HHAIDC from "@/components/HHAIDC.vue";
const routes = [
  { path: "/", component: HomePage, name: "HomePage" },
  { path: "/work", component: WorkPage, name: "WorkPage" },
  { path: "/work/nuday", component: NudayPage, name: "NudayPage" },
  { path: "/fun", component: FunPage, name: "FunPage" },
  { path: "/me", component: MePage, name: "MePage" },
  { path: "/identity", component: IdentityPage, name: "IdentityPage" },
  { path: "/hhaidc", component: HHAIDC, name: "HHAIDC" },
  { path: "/how-to-use-dp", component: UseDP, name: "UseDP" },
  {
    path: "/work/stable-install",
    component: StableInstallPage,
    name: "StableInstallPage",
  },
];
const router = createRouter({
  history: createWebHistory(),
  routes, // short for `routes: routes`
});

export default router;
